import React from 'react';
import { EditableText } from './editable_text';
import { IdText } from '../classes/text_id';
import { thisExpression } from '@babel/types';
import { QuestionTekCevap } from './question_TekCevap';
import { QuestionRating } from './question_Rating';
import { QuestionText } from './question_text';
import { QuestionCheckbox } from './question_Checkbox';
import { QuestionResimCevapli } from './question_ResimCevapli';
import { QuestionEvetHayir } from './question_EvetHayir';
import { QuestionSiralama } from './question_SiralamaSorusu';
import { QuestionTabloSoru } from './question_TabloSoru';
import { QuestionDropdown } from './question_Dropdown';
import { QuestionTarih } from './question_Tarih';

class Question extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        const { questionModel, resimDuzenle } = this.props;

        return (
            <>

                {
                    questionModel.imgUrl !== undefined ?
                        <img src={questionModel.imgUrl} />
                        :
                        ''
                }
                <div className={'question-buttons'}>
                    <button type="button" className={'order-bottom btn btn-info'} onClick={() => { this.props.yukari(this.props.questionModel.id) }}>↓</button>
                    <button type="button" className={'order-top btn btn-success'} onClick={() => { this.props.asagi(this.props.questionModel.id) }}>↑</button>
                    <button type="button" className={'delete-question btn btn-danger'} onClick={() => { this.props.sil(this.props.questionModel.id) }}>Sil</button>
                </div>
                <div className={'question-buttons-right'}>
                    <button type="button" className={'update-question btn btn-info'} onClick={() => { this.props.detay(this.props.questionModel) }}>Özellikler</button>
                </div>
                <div className={'col-md-12'} >
                    {
                        questionModel.typeId === 1 ?
                            <QuestionTekCevap questionModel={questionModel} />
                            :
                            questionModel.typeId === 2 ?
                                <QuestionRating questionModel={questionModel} />
                                :
                                questionModel.typeId === 3 ?
                                    <QuestionText questionModel={questionModel} />
                                    :
                                    questionModel.typeId === 4 ?
                                        <QuestionCheckbox questionModel={questionModel} />
                                        :
                                        questionModel.typeId === 5 ?
                                            <QuestionResimCevapli questionModel={questionModel} resimDuzenle={resimDuzenle} />
                                            :
                                            questionModel.typeId === 6 ?
                                                <QuestionEvetHayir questionModel={questionModel} />
                                                :
                                                questionModel.typeId === 7 ?
                                                    <QuestionSiralama questionModel={questionModel} />
                                                    :
                                                    questionModel.typeId === 8 ?
                                                        <QuestionTabloSoru questionModel={questionModel} />
                                                        :
                                                        questionModel.typeId === 10 || questionModel.typeId === 11 || questionModel.typeId === 12 || questionModel.typeId === 13  ?
                                                            <QuestionDropdown questionModel={questionModel} />
                                                            :
                                                            questionModel.typeId == 14 ?
                                                                <QuestionTarih questionModel={questionModel} />
                                                                :
                                                            ''
                    }

                </div>
            </>
        )
    }

}

export { Question }