import React from 'react';
import { EditableText } from './editable_text';
import { IdText } from '../classes/text_id';

class QuestionDropdown extends React.Component {
    constructor(props) {
        super(props);

        const cevap = new IdText("Cevap 1");
        const q = this.props.questionModel;
        debugger;
        this.state = {
            soru: q.text,
            cevaplar: q.answers,
            sira: q.order,
            required: q.required,
            maxSecim: q.maxSecim,
            minSecim: q.minSecim,
            q: q,
            dropDownTip: q.dropDownTip,
            tipler: q.tipler
        };

        this.cevapEkle = this.cevapEkle.bind(this);
        this.cevapSil = this.cevapSil.bind(this);
        this.cevapDegistir = this.cevapDegistir.bind(this);
        this.soruDegistir = this.soruDegistir.bind(this);
        this.tipDegistir = this.tipDegistir.bind(this);
    }

    cevapEkle() {
        const cevap = new IdText("Cevap " + (parseInt(this.state.cevaplar.length) + 1));
        let newCevaplar = this.state.cevaplar;
        newCevaplar.push(cevap);
        this.setState({ cevaplar: newCevaplar });
    }

    cevapSil(index) {
        let newCevaplar = this.state.cevaplar;
        newCevaplar.splice(index, 1);
        this.setState({ cevaplar: newCevaplar });
    }

    cevapDegistir(text, index) {
        let newCevaplar = this.state.cevaplar;
        newCevaplar[index].text = text;
        this.setState({ cevaplar: newCevaplar });
    }

    soruDegistir(text) {
        let q = this.state.q;
        q.text = text;
        this.setState({ q: q });
    }

    tipDegistir(e) {
        switch (e.target.value) {
            case 1:
                this.props.typeId = 10
                break;
            case 2:
                this.props.typeId = 11;
                break;
            case 3:
                this.props.typeId = 12;
                break;
            case 4:
                this.props.typeId = 13;
                break;
            default:
                this.props.typeId = 10;
                break;
        }
    }

    render() {
        return (
            <>
                <div>
                    <span>{this.props.questionModel.order}){<EditableText text={this.state.q.text} changeText={this.soruDegistir} />}</span>
                </div>
                <ul>
                    {this.state.dropDownTip == 1 &&
                        <div>
                        <select><option>İl</option></select>
                        <select><option>İlçe</option></select>
                        <select><option>Semt</option></select>
                        <select><option>Mahalle</option></select>
                        </div>
                    }
                    {this.state.dropDownTip == 2 &&
                        <div>
                            <select><option>İl</option></select>
                            <select><option>İlçe</option></select>
                            <select><option>Semt</option></select>
                        </div>
                    }
                    {this.state.dropDownTip == 3 &&
                        <div>
                            <select><option>İl</option></select>
                            <select><option>İlçe</option></select>
                        </div>
                    }
                    {this.state.dropDownTip == 4 &&
                        <div>
                            <select><option>İl</option></select>
                        </div>
                    }
                </ul>
            </>
        )
    }

}

export { QuestionDropdown }