import React from 'react';
import { connect } from 'react-redux';
import { surveyActions } from '../actions/survey.actions'; 
import { ImageCropper } from './image_cropper';


import {Button} from 'react-bootstrap';
import {Form} from 'react-bootstrap';
class FormQuestion extends React.Component {
    constructor(props) {
        super(props);  
        const{ model } = this.props;
        this.state = { 
            required: model.required,
            cols: model.cols, //50
            rows: model.rows, // 4
            maxLength: model.maxLength,
            minLength: model.minLength,
            maxSecim: model.maxSecim,
            minSecim: model.minSecim,
            typeId: model.typeId,
            imgUrl: model.imgUrl,
            id: model.id,
            profilSecim: model.profilSecim,
            kacAdetSiralasin: model.kacAdetSiralasin

            
        };
        this.handleInputChange = this.handleInputChange.bind(this);   
        this.soruDuzenle = this.soruDuzenle.bind(this);      
        this.resimKaydet = this.resimKaydet.bind(this);
        this.resimSil= this.resimSil.bind(this);
    } 


    handleInputChange(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
    
        this.setState({
          [name]: value
        });
      }

      resimKaydet(imgUrl){ 
        this.setState({
            imgUrl:  imgUrl
          });
    }

      soruDuzenle(event){  
        event.preventDefault();  
        this.props.soruDuzenleSubmit(this.state) 
        return false; 
    }
    resimSil(event){
    this.setState({
        imgUrl: undefined
    });
}
   


    render() { 
        const {typeId, required, cols, rows, maxLength, minLength, maxSecim, minSecim, imgUrl, profilSecim, kacAdetSiralasin} = this.state;  
        console.log(`gerekli? ${required}` );
        return (
            <Form onSubmit={this.soruDuzenle}> 
                {typeId === 3 && 
                    
                    <>
                        <Form.Group controlId="formCols">
                            <Form.Label>Text Genişliği</Form.Label>
                            <Form.Control type="text" placeholder="" value={cols} name="cols" onChange={this.handleInputChange}/>
                        </Form.Group>
                        <Form.Group controlId="formRows">
                            <Form.Label>Text Yüksekliği</Form.Label>
                            <Form.Control type="text" placeholder="" value={rows} name="rows" onChange={this.handleInputChange}/>
                        </Form.Group>
                        <Form.Group controlId="maxLength">
                            <Form.Label>Maximum Text Alanı</Form.Label>
                            <Form.Control type="text" placeholder="" name="maxLength" value={maxLength} onChange={this.handleInputChange}/>
                        </Form.Group>
                        <Form.Group controlId="minLength">
                            <Form.Label>Minimum Text Alanı</Form.Label>
                            <Form.Control type="text" placeholder="" name="minLength" value={minLength} onChange={this.handleInputChange}/>
                        </Form.Group>
                    </>
                    
                    } 
                    {(typeId === 4 ) &&
                        <>
                        <Form.Group controlId="maxSecim">
                            <Form.Label>Max Seçim Sayısı </Form.Label>
                            <Form.Control type="text" placeholder="" value={maxSecim} name="maxSecim" onChange={this.handleInputChange}/>
                        </Form.Group> 
                        <Form.Group controlId="minSecim">
                            <Form.Label>Min Seçim Sayısı</Form.Label>
                            <Form.Control type="text" placeholder="" value={minSecim} name="minSecim" onChange={this.handleInputChange}/>
                        </Form.Group>
                        </>
                    }  
                    { (typeId === 7) &&
                        <>
                        <Form.Group controlId="maxSecim">
                            <Form.Label>Kaç Adet Sıralasın</Form.Label>
                            <Form.Control type="text" placeholder="" value={kacAdetSiralasin} name="kacAdetSiralasin" onChange={this.handleInputChange}/>
                        </Form.Group>  
                        </>
                    } 
            <ImageCropper resimKaydet={this.resimKaydet} croppedImageUrl={imgUrl}/>
            {imgUrl !== undefined &&
                 <>
                    <hr />
                    <button onClick={this.resimSil} type="button" className="btn btn-primary btn-lg btn-block">
                       Resmi Sil
                  </button>  
              <hr />
              </>
              }
    <hr />
            
    <Form.Group controlId="exampleForm.ControlSelect1">
    <Form.Label>Profile Bağla</Form.Label>
    <Form.Control as="select" name="profilSecim" onChange={this.handleInputChange} selected>
    <option value="">-</option>
        {
        window.profilSutunlari.map((sutun) =>{
            return <option selected={profilSecim === sutun.value} value={sutun.value} >{sutun.text}</option>
         })
        }
    </Form.Control>
  </Form.Group>


            <Form.Group controlId="formRequired">
                <Form.Check type="checkbox" label="Doldurulması Zorunlu Mu?" value={required? 'on' : 'off'} name="required"  checked={required} onChange={this.handleInputChange}/>
            </Form.Group>
            


            <Button variant="primary" type="submit">
                 Kaydet
            </Button>
          </Form>
        )
    }

}

export { FormQuestion }