import {
    surveyConstants
} from '../constants';
import {
    CHECKBOX_SORU,
    TEXT_SORU,
    RATING_SORU,
    TEKCEVAP_SORU,
    RESIM_SORU,
    EVETHAYIR_SORU,
    SIRALAMA_SORU,
    TABLO_SORU,
    DROPDOWN_SORU,
    TARIH_SORU
} from '../constants/question.constants';
import uuid from 'uuid';
export function survey(state = window.anketData, action) {
    var newOrder = state.questions.length + 1;
    switch (action.type) {
        case surveyConstants.RESIM_CEVAPLI_SORU_EKLE: {
            var question = RESIM_SORU(newOrder);
            return {
                ...state,
                questions: [...state.questions, question]
            }
        }
        case surveyConstants.CHECKBOX_SORU_EKLE: {
            var question = CHECKBOX_SORU(newOrder);
            return {
                ...state,
                questions: [...state.questions, question]
            }
        }
        case surveyConstants.TEXT_SORU_EKLE: {
            var question = TEXT_SORU(newOrder);
            return {
                ...state,
                questions: [...state.questions, question]
            }
        }
        case surveyConstants.TEKCEVAP_SORU_EKLE: {
            var question = TEKCEVAP_SORU(newOrder);
            return {
                ...state,
                questions: [...state.questions, question]
            }
        }
        case surveyConstants.RATING_SORU_EKLE: {
            var question = RATING_SORU(newOrder);
            return {
                ...state,
                questions: [...state.questions, question]
            }
        }
        case surveyConstants.EVETHAYIR_SORU_EKLE: {
            var question = EVETHAYIR_SORU(newOrder);
            return {
                ...state,
                questions: [...state.questions, question]
            }
        }
        case surveyConstants.SIRALAMA_SORU_EKLE: {
            var question = SIRALAMA_SORU(newOrder);
            return {
                ...state,
                questions: [...state.questions, question]
            }
        }
        case surveyConstants.TABLO_SORU_EKLE: {
            var question = TABLO_SORU(newOrder);
            return {
                ...state,
                questions: [...state.questions, question]
            }
        }

        case surveyConstants.DROPDOWN_SORU_EKLE_1: {
            var question = DROPDOWN_SORU(newOrder,1 );
            return {
                ...state,
                questions: [...state.questions, question]
            }
        }

        case surveyConstants.DROPDOWN_SORU_EKLE_2: {
            var question = DROPDOWN_SORU(newOrder, 2);
            return {
                ...state,
                questions: [...state.questions, question]
            }
        }

        case surveyConstants.DROPDOWN_SORU_EKLE_3: {
            var question = DROPDOWN_SORU(newOrder, 3);
            return {
                ...state,
                questions: [...state.questions, question]
            }
        }

        case surveyConstants.DROPDOWN_SORU_EKLE_4: {
            var question = DROPDOWN_SORU(newOrder, 4);
            return {
                ...state,
                questions: [...state.questions, question]
            }
        }

        case surveyConstants.TARIH_SORUSU_EKLE: {
            var question = TARIH_SORU(newOrder);
            return {
                ...state,
                questions: [...state.questions, question]
            }
        }

        case surveyConstants.SORU_SIL: {
            const newList = state.questions.filter(function (q) {
                return q.id !== action.id
            });
            sort(newList).forEach(function (element, i) {
                element.order = (i + 1);
            });

            return {
                ...state,
                questions: newList
            }
        }


        case surveyConstants.SORU_YUKARI: {
            const newList1 = [...state.questions];
            const newItem1 = newList1.filter(function (q) {
                return q.id === action.id
            });
            newItem1[0].order += 1.1;
            sort(newList1).forEach(function (element, i) {
                element.order = (i + 1);
            });
            return {
                ...state,
                questions: sort(newList1)
            }
        }
        case surveyConstants.SORU_ASAGI: {
            const newList2 = [...state.questions];
            const newItem2 = newList2.filter(function (q) {
                return q.id === action.id
            });
            newItem2[0].order -= 1.1;
            sort(newList2).forEach(function (element, i) {
                element.order = (i + 1);
            });

            return {
                ...state,
                questions: sort(newList2)
            }
        }
        case surveyConstants.SORU_DUZENLE: {
            const sorular = state.questions.map((item, index) => {
                if (item.id !== action.payload.id) {
                    // This isn't the item we care about - keep it as-is
                    return item
                }

                // Otherwise, this is the one we want - return an updated value
                return {
                    ...item,
                    ...action.payload
                }
            })
            return {
                ...state,
                questions: sorular
            }
        }
        case surveyConstants.SORU_CEVAP_DUZENLE: {
            // sorucevap düzenle 
            state.questions.map((item, index) => {
                item.answers = item.answers.map((answer, i) => {
                    if (answer.id !== action.payload.id) {
                        // This isn't the item we care about - keep it as-is
                        return answer
                    }
                    // Otherwise, this is the one we want - return an updated value
                    return {
                        ...answer,
                        ...action.payload
                    }
                });
            })
            return {
                ...state
            }
        }
        default:
            return {
                ...state
            };
    }
}

function sort(obj) {
    return obj.slice().sort(function (a, b) {
        if (a.order < b.order)
            return -1
        if (a.order > b.order)
            return 1
        return 0
    })
}