import React from 'react';
import { EditableText } from './editable_text';
import { IdText } from '../classes/text_id'; 

class QuestionText extends React.Component {
    constructor(props) {
        super(props);
         
        const q =this.props.questionModel;
        this.state = {
            soru: q.text,
            cevaplar: q.answers,
            sira: q.order,
            placeholder: q.placeholder,
            cols:q.cols,
            rows:q.rows,
            q: q
        };  
        this.placeholderDegistir = this.placeholderDegistir.bind(this);
        this.soruDegistir = this.soruDegistir.bind(this);
    }
 
    placeholderDegistir(e, index) {  
        const { value } = e.target;
        this.setState({ placeholder: value });
    }
    

    soruDegistir(text) {
        let q = this.state.q;
        q.text = text; 
        this.setState({ q: q });
    }

    render() { 
        return (
            <>
                <div>
                    <span>{this.props.questionModel.order}){<EditableText text={this.state.q.text}  changeText={this.soruDegistir} />}</span>
                </div> 
                <div className={"btn-group btn-group-toggle"}> 
                    <textarea type="text" placeholder={this.state.placeholder} cols={this.state.cols} rows={this.state.rows} onChange={this.placeholderDegistir}></textarea> 
                </div> 
            </>
        )
    }

}

export { QuestionText }