import React from 'react';
import { EditableText } from './editable_text';
import { IdText } from '../classes/text_id'; 

class QuestionResimCevapli extends React.Component {
    constructor(props) {
        super(props);
        
        const cevap = new IdText("Cevap 1");
        const q =this.props.questionModel;
        this.state = {
            soru: q.text,
            cevaplar: q.answers,
            sira: q.order,
            required: q.required,
            maxSecim: q.maxSecim,
            minSecim: q.minSecim,
            q: q
        }; 

        this.cevapEkle = this.cevapEkle.bind(this);
        this.cevapSil = this.cevapSil.bind(this);
        this.cevapDegistir = this.cevapDegistir.bind(this);
        this.soruDegistir = this.soruDegistir.bind(this);
        this.resimDuzenle = this.resimDuzenle.bind(this);
    }

    cevapEkle() {
        const cevap = new IdText("Cevap " + (parseInt(this.state.cevaplar.length) + 1));
        let newCevaplar = this.state.cevaplar;
        newCevaplar.push(cevap);
        this.setState({ cevaplar: newCevaplar });
    }

    cevapSil(index) {
        let newCevaplar = this.state.cevaplar;
        newCevaplar.splice(index, 1);
        this.setState({ cevaplar: newCevaplar });
    }

    cevapDegistir(text, index) {
        let newCevaplar = this.state.cevaplar;
        newCevaplar[index].text = text;
        this.setState({ cevaplar: newCevaplar });
    }

    soruDegistir(text) {
        let q = this.state.q;
        q.text = text; 
        this.setState({ q: q });
    }
    resimDuzenle(cevap){
        this.props.resimDuzenle(cevap, this.state.id);
    }

    render() {  
        const q = this.props.questionModel;
        return (
            <>
                <div>
                    <span>{this.props.questionModel.order}){<EditableText text={this.state.q.text}  changeText={this.soruDegistir} />}</span>
                </div>
                <ul>
                    {/* {q.answers.map((cevap, i) => { */}
                    {this.state.cevaplar.map((cevap, i) => {
                        return <li key={'q_'+this.props.questionModel.id +'cevap-' + i}> 
                            {
                                cevap.imgUrl === undefined ?
                                <>
                                  <hr />
                                    <button onClick={() => {this.resimDuzenle(cevap)}} type="button" className="btn btn-primary btn-lg">
                                        Resim Ekle
                                    </button>  
                                  <hr />
                                </>
                                :
                                <>
                                <hr />
                                  <img alt={"Resim Düzenle"} src={cevap.imgUrl} onClick={() => {this.resimDuzenle(cevap)}} style={{maxWidth:'100px', maxHeight: '100px'}} />
                                <hr />
                                </> 
                            } 
                            <button type="button" onClick={() => this.cevapSil(i)}>-</button>
                        </li>
                    })}
                    <button type="button" onClick={this.cevapEkle}>+</button>
                </ul>
            </>
        )
    }

}

export { QuestionResimCevapli }