import React from 'react';
import { connect } from 'react-redux';
import { Soru } from '../classes/soru';
import { surveyActions } from '../actions/survey.actions';
import { FormQuestion } from './form_question';
import { FormAnswer } from './form_answer';
import { Question } from './question';
import { Button, Collapse } from 'react-bootstrap'
import Modal from 'react-bootstrap/Modal';


class SurveyContainer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            show: false,
            showAnswer: false,
            activeQuestion: null,
            showAdresCollapse: false
        };
        this.checkboxSoruEkle = this.checkboxSoruEkle.bind(this);
        this.tekCevapSoruEkle = this.tekCevapSoruEkle.bind(this);
        this.ratingSoruEkle = this.ratingSoruEkle.bind(this);
        this.textSoruEkle = this.textSoruEkle.bind(this);
        this.evetHayirSoruEkle = this.evetHayirSoruEkle.bind(this);
        this.soruSil = this.soruSil.bind(this);
        this.soruAsagi = this.soruAsagi.bind(this);
        this.soruYukari = this.soruYukari.bind(this);
        this.export = this.export.bind(this);
        this.setShow = this.setShow.bind(this);
        this.setShowAnswer = this.setShowAnswer.bind(this);
        this.soruDetay = this.soruDetay.bind(this);
        this.cevapResimDetay = this.cevapResimDetay.bind(this);
        this.soruDuzenle = this.soruDuzenle.bind(this);
        this.soruCevapDuzenle = this.soruCevapDuzenle.bind(this);
        this.resimCevapliSoruEkle = this.resimCevapliSoruEkle.bind(this);
        this.siralamaSoruEkle = this.siralamaSoruEkle.bind(this);
        this.tabloSoruEkle = this.tabloSoruEkle.bind(this);
        this.dropdownSoruEkle = this.dropdownSoruEkle.bind(this);
        this.tarihSorusuEkle = this.tarihSorusuEkle.bind(this);
        this.adresCollapseToggle = this.adresCollapseToggle.bind(this);

    }
    setShow(b, q) {
        if (b) {
            this.setState({
                activeQuestion: q,
                show: b
            });
        } else {
            this.setState({
                activeQuestion: null,
                show: b
            })
        }
    }
    setShowAnswer(b, a) {
        if (a) {
            this.setState({
                activeAnswer: a,
                showAnswer: b
            })
        }
        else {
            this.setState({
                activeAnswer: null,
                showAnswer: b
            })
        }
    }

    resimCevapliSoruEkle() {
        const { dispatch } = this.props;
        dispatch(dispatch => {
            dispatch(surveyActions.resimCevapliSoruEkle());
        });
    }

    checkboxSoruEkle() {
        const { dispatch } = this.props;
        dispatch(dispatch => {
            dispatch(surveyActions.checkboxSoruEkle());
        });
    }

    textSoruEkle() {
        const { dispatch } = this.props;
        dispatch(dispatch => {
            dispatch(surveyActions.textSoruEkle());
        });
    }
    evetHayirSoruEkle() {
        const { dispatch } = this.props;
        dispatch(dispatch => {
            dispatch(surveyActions.evetHayirSoruEkle());
        });
    }
    tekCevapSoruEkle() {
        const { dispatch } = this.props;
        dispatch(dispatch => {
            dispatch(surveyActions.tekCevapSoruEkle());
        });
    }
    ratingSoruEkle() {
        const { dispatch } = this.props;
        dispatch(dispatch => {
            dispatch(surveyActions.ratingSoruEkle());
        });
    }

    siralamaSoruEkle() {
        const { dispatch } = this.props;
        dispatch(dispatch => {
            dispatch(surveyActions.siralamaSoruEkle());
        });
    }

    tabloSoruEkle() {
        const { dispatch } = this.props;
        dispatch(dispatch => {
            dispatch(surveyActions.tabloSoruEkle());
        });
    }

    dropdownSoruEkle(tipId) {
        const { dispatch } = this.props;
        dispatch(dispatch => {
            dispatch(surveyActions.dropDownSoruEkle(tipId));
        });
    }

    tarihSorusuEkle() {
        const { dispatch } = this.props;
        dispatch(dispatch => {
            dispatch(surveyActions.tarihSorusuEkle());
        });
    }

    soruSil(id) {
        const { dispatch } = this.props;
        dispatch(dispatch => {
            dispatch(surveyActions.soruSil(id));
        });
    }
    soruYukari(id) {
        const { dispatch } = this.props;
        dispatch(dispatch => {
            dispatch(surveyActions.soruYukari(id));
        });
    }
    soruAsagi(id) {
        const { dispatch } = this.props;
        dispatch(dispatch => {
            dispatch(surveyActions.soruAsagi(id));
        });
    }

    soruDetay(soru) {
        this.setShow(true, soru);
    }
    cevapResimDetay(cevap) {
        this.setShowAnswer(true, cevap);
    }

    soruDuzenle(obj) {
        const { dispatch } = this.props;
        dispatch(dispatch => {
            dispatch(surveyActions.soruDuzenle(obj));
        });
        this.setShow(false);
        return false;
    }
    soruCevapDuzenle(obj) {
        const { dispatch } = this.props;
        dispatch(dispatch => {
            dispatch(surveyActions.soruCevapDuzenle(obj));
        });
        this.setShowAnswer(false);
        return false;
    }

    adresCollapseToggle() {
        this.setState({ showAdresCollapse: !this.state.showAdresCollapse });
    }

    export(id) {
        console.log(this.props.survey);
        window.anketKaydet && window.anketKaydet(this.props.survey);
    }


    render() {
        const { questions } = this.props.survey;

        return (
            <div className={'container-fluid'}>
                <div className={'row'}>

                    <div className={'col-md-3 panel'}>
                        <h2 className={'mb-5'}>
                            SORU EKLE
                        </h2>
                        <div>
                            <button onClick={this.evetHayirSoruEkle} type="button" className="btn btn-info btn-question btn-block ">
                                <svg viewBox="0 0 16 16" id="icon-boolean"><path d="M0 16h10V6H0v10zm2-6l2 2 4-4 1 1-5 5-3-3 1-1z"></path><path d="M5 1v4h1V2h8v8h-3v1h4V1z"></path></svg>
                                Evet - Hayır</button>
                        </div>
                        <hr />
                        <div>
                            <button onClick={this.tekCevapSoruEkle} type="button" className="btn btn-info btn-question btn-block ">
                                <svg viewBox="0 0 16 16" id="icon-matrix"><path d="M4 1C2.3 1 1 2.3 1 4s1.3 3 3 3 3-1.3 3-3-1.3-3-3-3zm0 5c-1.1 0-2-.9-2-2s.9-2 2-2 2 .9 2 2-.9 2-2 2z"></path><circle cx="4" cy="4" r="1"></circle><path d="M12 7c1.7 0 3-1.3 3-3s-1.3-3-3-3-3 1.3-3 3 1.3 3 3 3zm0-5c1.1 0 2 .9 2 2s-.9 2-2 2-2-.9-2-2 .9-2 2-2zM4 9c-1.7 0-3 1.3-3 3s1.3 3 3 3 3-1.3 3-3-1.3-3-3-3zm0 5c-1.1 0-2-.9-2-2s.9-2 2-2 2 .9 2 2-.9 2-2 2zM12 9c-1.7 0-3 1.3-3 3s1.3 3 3 3 3-1.3 3-3-1.3-3-3-3zm0 5c-1.1 0-2-.9-2-2s.9-2 2-2 2 .9 2 2-.9 2-2 2z"></path></svg>
                                Tek cevaplı</button>
                        </div>
                        <hr />
                        <div>
                            <button onClick={this.ratingSoruEkle} type="button" className="btn btn-info btn-question btn-block ">
                                <svg viewBox="0 0 16 16" id="icon-rating"><path d="M0 7h1l1-1v5h1V5H2L0 7zm5 5h5V4H5v8zm1-5h2V6H6V5h3v3H7v2h2v1H6V7zm6-2v1h2v1h-2v1h2v2h-2v1h3V5h-3z"></path></svg>
                                Puanlandırmalı</button>

                        </div>
                        <hr />
                        <div>
                            <button onClick={this.textSoruEkle} type="button" className="btn btn-info btn-question btn-block">
                                <svg viewBox="0 0 16 16" id="icon-comment"><path d="M8 2C4.1 2 1 4.2 1 7c0 1.9 1.5 3.6 3.8 4.4C4.6 13.1 3 14 3 14s1.5-.2 2.7-.9c.4-.2.9-.8 1.3-1.2.3.1.6.1 1 .1 3.9 0 7-2.2 7-5s-3.1-5-7-5zM4 8c-.6 0-1-.4-1-1s.4-1 1-1 1 .4 1 1-.4 1-1 1zm4 0c-.6 0-1-.4-1-1s.4-1 1-1 1 .4 1 1-.4 1-1 1zm4 0c-.6 0-1-.4-1-1s.4-1 1-1 1 .4 1 1-.4 1-1 1z"></path></svg>
                                Text</button>

                        </div>
                        <hr />
                        <div>
                            <button onClick={this.checkboxSoruEkle} type="button" className="btn btn-info btn-question btn-block">
                                <svg viewBox="0 0 16 16" id="icon-matrixdropdown"><path d="M4 1C2.3 1 1 2.3 1 4s1.3 3 3 3 3-1.3 3-3-1.3-3-3-3zm0 5c-1.1 0-2-.9-2-2s.9-2 2-2 2 .9 2 2-.9 2-2 2z"></path><circle cx="4" cy="4" r="1"></circle><path d="M12 7c1.7 0 3-1.3 3-3s-1.3-3-3-3-3 1.3-3 3 1.3 3 3 3zm0-5c1.1 0 2 .9 2 2s-.9 2-2 2-2-.9-2-2 .9-2 2-2zM4 9c-1.7 0-3 1.3-3 3s1.3 3 3 3 3-1.3 3-3-1.3-3-3-3zm0 5c-1.1 0-2-.9-2-2s.9-2 2-2 2 .9 2 2-.9 2-2 2z"></path><circle cx="4" cy="12" r="1"></circle><path d="M12 9c-1.7 0-3 1.3-3 3s1.3 3 3 3 3-1.3 3-3-1.3-3-3-3zm0 5c-1.1 0-2-.9-2-2s.9-2 2-2 2 .9 2 2-.9 2-2 2z"></path></svg>
                                Çoklu seçim</button>
                        </div>

                        {
                            /*
                         <hr />
                                    <button onClick={this.siralamaSoruEkle} type="button" className="btn btn-info btn-question btn-block">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M6 3l-6 8h4v10h4v-10h4l-6-8zm16 6h-8v-2h8v2zm2-6h-10v2h10v-2zm-4 8h-6v2h6v-2zm-2 4h-4v2h4v-2zm-2 4h-2v2h2v-2z"/></svg>
                                    Sıralama</button> 
            */
                        }


                        <hr />
                        <button onClick={this.resimCevapliSoruEkle} type="button" className="btn btn-info btn-question btn-block">
                            <svg viewBox="0 0 16 16" id="icon-imagepicker"><path d="M15 14H0V1h15v13zM1 13h13V2H1v11z"></path><path d="M2 3v9h11V3H2zm4 1c.6 0 1 .4 1 1s-.4 1-1 1-1-.4-1-1 .4-1 1-1zm-3 7l2-3 1 1 2-3 4 5H3z"></path></svg>
                            Resimli
                        </button>
                        <hr />
                        <button onClick={this.tabloSoruEkle} type="button" className="btn btn-info btn-question btn-block">
                            <svg viewBox="0 0 16 16" id="icon-paneldynamic"><path d="M2 8h10v1H2zM2 11h8l1-1H2zM9 16h2l-2-2zM14 9l-4 4 2 2 4-4zM3 3v2H2l2 2 2-2H5V3zM11 5V3H9v2H8l2 2 2-2z"></path><path d="M1 1h12v7l1-1V0H0v14h7l1-1H1z"></path></svg>
                            Tablo Soru
                        </button>

                        <div style={{ "position": "fixed", "bottom": "20px", "right": "20px", "width": "100px", "zIndex": "5" }}>
                            <button onClick={this.export} type="button" className="btn btn-info btn-question btn-block">KAYDET</button>
                        </div>
                        <hr />
                        <div>
                            <div style={{ marginBottom: '12px' }}>
                            <button onClick={this.adresCollapseToggle} type="button" className="btn btn-info btn-question btn-block">
                                <svg viewBox="0 0 16 16" id="icon-matrix_dropdown"><path d="M1 1v6h14V1H1zm10 5L8 3l1-1 2 2 2-2 1 1-3 3zm-8 5h12V9H3v2zm0 4h12v-2H3v2z"></path></svg>
                                    Adres Sorusu </button>
                            </div>

                            <div className={(this.state.showAdresCollapse ? 'show' : '') + ' collapse'}>
                                <button onClick={() => this.dropdownSoruEkle(1)} type="button" className="btn btn-info btn-question btn-block">
                                    Tam Adres</button>
                                <button onClick={() => this.dropdownSoruEkle(2)} type="button" className="btn btn-info btn-question btn-block">
                                    Mahalle Hariç</button>
                                <button onClick={() => this.dropdownSoruEkle(3)} type="button" className="btn btn-info btn-question btn-block">
                                    Mahalle ve Semt Hariç</button>
                                <button onClick={() => this.dropdownSoruEkle(4)} type="button" className="btn btn-info btn-question btn-block">
                                    Mahalle, Semt ve İlçe Hariç</button>
                            </div>
                        </div>
                        <hr />
                        <button onClick={this.tarihSorusuEkle} type="button" className="btn btn-info btn-question btn-block">
                            <svg viewBox="0 0 16 16" id="icon-text"><path d="M2 1v3h1V3h4v10H5v1h6v-1H9V3h4v1h1V1H2z"></path></svg>
                            Tarih Sorusu
                        </button>
                    </div>
                    <div className={'col-md-9 panel'}>
                        <div className={'survey--container'}>
                            {
                                questions.map((question, index) => {
                                    return (
                                        <div className={'survey--question'} key={'questionss_' + question.id}>

                                            <Question questionModel={question} sil={this.soruSil} yukari={this.soruYukari} asagi={this.soruAsagi} detay={this.soruDetay} resimDuzenle={this.cevapResimDetay} />

                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                </div>

                <>
                    <Modal
                        show={this.state.show}
                        onHide={() => this.setShow(false)}
                        dialogClassName="modal-90w modal-lg"
                        aria-labelledby="example-custom-modal-styling-title"
                    >
                        <Modal.Header closeButton>
                            <Modal.Title id="example-custom-modal-styling-title">
                                {this.state.activeQuestion && this.state.activeQuestion.type} DÜZENLE
                    </Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <FormQuestion model={this.state.activeQuestion} soruDuzenleSubmit={this.soruDuzenle} />
                        </Modal.Body>
                    </Modal>
                </>


                <>
                    <Modal
                        show={this.state.showAnswer}
                        onHide={() => this.setShowAnswer(false)}
                        dialogClassName="modal-90w modal-lg"
                        aria-labelledby="example-custom-modal-styling-title"
                    >
                        <Modal.Header closeButton>
                            <Modal.Title id="example-custom-modal-styling-title">
                                {this.state.activeAnswer && <b>CEVAP RESİM EKLE</b>}
                            </Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <FormAnswer model={this.state.activeAnswer} soruCevapDuzenleSubmit={this.soruCevapDuzenle} />
                        </Modal.Body>
                    </Modal>
                </>
            </div>



        );
    }
}
function mapStateToProps(state) {
    const { survey } = state;
    return {
        survey
    };
}

const connectedSurveyContainer = connect(mapStateToProps)(SurveyContainer);
export { connectedSurveyContainer as SurveyContainer };