import { surveyConstants } from '../constants';

export const surveyActions = {
    tekCevapSoruEkle,
    ratingSoruEkle,
    textSoruEkle,
    checkboxSoruEkle,
    resimCevapliSoruEkle,
    soruSil,
    soruYukari,
    soruAsagi,
    soruDetay,
    soruDuzenle,
    soruCevapDuzenle,
    evetHayirSoruEkle,
    siralamaSoruEkle,
    tabloSoruEkle,
    dropDownSoruEkle,
    tarihSorusuEkle
};
function textSoruEkle() {
    return {
        type: surveyConstants.TEXT_SORU_EKLE
    };
}
function checkboxSoruEkle() {
    return {
        type: surveyConstants.CHECKBOX_SORU_EKLE
    };
}
function tekCevapSoruEkle() {
    return {
        type: surveyConstants.TEKCEVAP_SORU_EKLE
    };
}
function ratingSoruEkle() {
    return {
        type: surveyConstants.RATING_SORU_EKLE
    };
}
function soruSil(index) {
    return {
        type: surveyConstants.SORU_SIL,
        id: index
    };
}
function soruYukari(index) {
    return {
        type: surveyConstants.SORU_YUKARI,
        id: index
    };
}
function soruAsagi(index) {
    return {
        type: surveyConstants.SORU_ASAGI,
        id: index
    };
}
function soruDetay(index) {
    return {
        type: surveyConstants.SORU_DETAY,
        id: index
    }
}

function soruDuzenle(obj) {
    return {
        type: surveyConstants.SORU_DUZENLE,
        payload: obj
    }
}

function soruCevapDuzenle(obj) {
    return {
        type: surveyConstants.SORU_CEVAP_DUZENLE,
        payload: obj
    }
}

function resimCevapliSoruEkle() {
    return {
        type: surveyConstants.RESIM_CEVAPLI_SORU_EKLE

    }
}
function evetHayirSoruEkle() {
    return {
        type: surveyConstants.EVETHAYIR_SORU_EKLE
    }
}
function siralamaSoruEkle() {
    return {
        type: surveyConstants.SIRALAMA_SORU_EKLE
    }
}
function tabloSoruEkle() {
    return {
        type: surveyConstants.TABLO_SORU_EKLE
    };
}

function dropDownSoruEkle(tipId) {
    switch (tipId) {
        case 1:
            return {
                type: surveyConstants.DROPDOWN_SORU_EKLE_1
            };
        case 2:
            return {
                type: surveyConstants.DROPDOWN_SORU_EKLE_2
            };
        case 3:
            return {
                type: surveyConstants.DROPDOWN_SORU_EKLE_3
            };
        case 4:
            return {
                type: surveyConstants.DROPDOWN_SORU_EKLE_4
            };
        default:
            return {
                type: surveyConstants.DROPDOWN_SORU_EKLE_1
            };

    }
}

function tarihSorusuEkle() {
    return {
        type: surveyConstants.TARIH_SORUSU_EKLE
    }
}