import React from 'react';

class EditableText extends React.Component {
    constructor(props) {
        super(props);
        this.state = { text: this.props.text, editMode: false, newText: '' }

        this.handleChangeText = this.handleChangeText.bind(this);
        this.startEdit = this.startEdit.bind(this);
        this.confirmEdit = this.confirmEdit.bind(this);
        this.cancelEdit = this.cancelEdit.bind(this);
        this.endEdit = this.endEdit.bind(this);
        this.handleFocus = this.handleFocus.bind(this);
    }

    startEdit() {
        this.setState({ editMode: true, newText: this.props.text });  
    }

    endEdit(e) {
        switch (e.keyCode) {
            case 27:
                this.cancelEdit();
                break;
            case 13:
                this.confirmEdit();
                break;
            default:
                break;
        }
    }

    confirmEdit() {
        this.props.changeText(this.state.newText, this.props.index);
        this.setState({ editMode: false });
    }

    cancelEdit() {
        this.setState({ editMode: false });
    }

    handleChangeText(e) {
        const { value } = e.target;
        this.setState({ newText: value });
    }
    handleFocus(e) {
        e.target.select();
      }

    render() {
        return (
            this.state.editMode ?
                <input autoFocus value={this.state.newText} onChange={this.handleChangeText} onKeyDown={this.endEdit} onBlur={this.confirmEdit} onFocus={this.handleFocus} />
                :
                <span onClick={this.startEdit}>{this.props.text}</span>
        )
    }
}

export { EditableText }