import React from 'react';
import { EditableText } from './editable_text';
import { IdText } from '../classes/text_id'; 

class QuestionTabloSoru extends React.Component {
    constructor(props) {
        super(props);
        
        const cevap = new IdText("Cevap 1");
        const q =this.props.questionModel;
        this.state = {
            soru: q.text,
            cevaplar: q.answers,
            sira: q.order,
            required: q.required,
            sorular: q.questions,
            q: q
        };


        this.cevapEkle = this.cevapEkle.bind(this);
        this.cevapSil = this.cevapSil.bind(this);
        this.cevapDegistir = this.cevapDegistir.bind(this);
        this.anaSoruDegistir = this.anaSoruDegistir.bind(this);
        this.soruDegistir = this.soruDegistir.bind(this);
        this.soruEkle = this.soruEkle.bind(this);
        this.soruSil = this.soruSil.bind(this);
    }

    cevapEkle() {
        const cevap = new IdText("Cevap " + (parseInt(this.state.cevaplar.length) + 1));
        let newCevaplar = this.state.cevaplar;
        newCevaplar.push(cevap);
        this.setState({ cevaplar: newCevaplar });
    }

    cevapSil(index) {
        let newCevaplar = this.state.cevaplar;
        newCevaplar.splice(index, 1);
        this.setState({ cevaplar: newCevaplar });
    }

    cevapDegistir(text, index) {
        let newCevaplar = this.state.cevaplar;
        newCevaplar[index].text = text;
        this.setState({ cevaplar: newCevaplar });
    }

    anaSoruDegistir(text) {
        let q = this.state.q;
        q.text = text; 
        this.setState({ q: q });
    }

    soruDegistir(text, index) { 
        let newSorular = this.state.sorular;
        newSorular[index].text = text;
        this.setState({ sorular: newSorular });
    }
    soruEkle() {
        const soru = new IdText("Soru " + (parseInt(this.state.sorular.length) + 1));
        let newSorular = this.state.sorular;
        newSorular.push(soru);
        this.setState({ sorular: newSorular });
    }

    soruSil(index) {
        let newSorular = this.state.sorular;
        newSorular.splice(index, 1);
        this.setState({ sorular: newSorular });
    }

    render() { 
        return (
            <> 
                <div>
                    <strong>{this.props.questionModel.order}){<EditableText text={this.state.q.text}  changeText={this.anaSoruDegistir} />}</strong>
                </div>
                <div className="row">
                     <div className="col-md-6">
                         <strong>SORULAR</strong>
                         <ul>
                                {this.state.sorular.map((soru, i) => {
                                    return <li key={'q_'+this.props.questionModel.id +'soru-' + i}>
                                        <EditableText index={i} text={soru.text} changeText={this.soruDegistir} />  
                                        <button type="button" onClick={() => this.soruSil(i)}>-</button>
                                    </li>
                                })}
                            <button type="button" onClick={this.soruEkle}>+</button>
                        </ul>
                        
                     </div>
                     <div className="col-md-6">
                         <strong>CEVAPLAR</strong>
                         <ul>
                            {this.state.cevaplar.map((cevap, i) => {
                                return <li key={'q_'+this.props.questionModel.id +'cevap-' + i}>
                                    <input type="radio" name={'cevap-' + this.props.id} />
                                    <EditableText index={i} text={cevap.text} changeText={this.cevapDegistir} />  
                                    <button type="button" onClick={() => this.cevapSil(i)}>-</button>
                                </li>
                            })}
                            <button type="button" onClick={this.cevapEkle}>+</button>
                        </ul>
                     </div>
                </div>
               
            </>
        )
    }

}

export { QuestionTabloSoru }