import React from 'react'; 
import { ImageCropper } from './image_cropper';


import {Button} from 'react-bootstrap';
import {Form} from 'react-bootstrap';
class FormAnswer extends React.Component {
    constructor(props) {
        super(props);  
        const{ model } = this.props; 
        this.state = {  
            imgUrl: model.imgUrl,
            id: model.id 
        };   
        this.soruCevapDuzenle = this.soruCevapDuzenle.bind(this);      
        this.cevapResimKaydet = this.cevapResimKaydet.bind(this);
        this.cevapResimSil= this.cevapResimSil.bind(this);
    } 
    cevapResimKaydet(imgUrl){ 
        this.setState({
            imgUrl:  imgUrl
          });
    } 
      soruCevapDuzenle(event){  
        event.preventDefault();  
        
        this.props.model.imgUrl = this.state.imgUrl;
        this.props.soruCevapDuzenleSubmit(this.state) 
        return false; 
    }
    cevapResimSil(event){
    this.setState({
        imgUrl: undefined
    });
}
   


    render() {
        const {imgUrl} = this.state;   
        return (
            <Form onSubmit={this.soruCevapDuzenle}> 
            
            <ImageCropper resimKaydet={this.cevapResimKaydet} croppedImageUrl={imgUrl}/>
            {imgUrl !== undefined &&
                 <>
                    <hr />
                    <button onClick={this.cevapResimSil} type="button" className="btn btn-primary btn-lg btn-block">
                       Resmi Sil
                  </button>  
                     <hr />
                </>
              }
    <hr /> 
 
            <Button variant="primary" type="submit">
                 Kaydet
            </Button>
          </Form>
        )
    }

}

export { FormAnswer }